.form__headline {
  font-size: 14px;
  line-height: 18px;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 39px;
  padding: 0;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
    width: 630px;
    margin-bottom: 66px;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
  }
}
