.promo__button {
  display: block;
  font-size: 14px;
  line-height: 40px;
  list-style: none;
  text-decoration: none;
  padding: 0;
  margin: 0;

  &:hover {
    background-color: @green-h;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 54px;
    width: 244px;
    height: 54px;
    z-index: 2;
    order: 2;
  }
}
